.CreateTodoButton {
  background-color: #61DAFA;
  box-shadow: 0 5px 25px rgba(97, 218, 250, 0.5);
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 50px;
  position: fixed;
  bottom: 24px;
  right: 24px;
  font-weight: bold;
  color: #FAFAFA;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  cursor: pointer;

  transform: rotate(0);
  trasition:  .3s ease;
}

.CreateTodoButton:hover {
  transform: rotate(224deg);
}